body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  font-size: 130%;
}
.App {
  width: 100vw;
  height: 100vh;
  background: #f8f8f8;
  display: flex;
  justify-content: center;
  align-items: center;
}

#AppContainer {
    background-color: #00b5ad;
    border-radius: 3%;
    padding: 0 3%;
    width: 40vw;
}
#Header {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
}

#Header > #title {
    color: white;
    font-size: 350%;
    margin-bottom: 1%;
    font-weight: 100;
}

#Header > hr {
    color: white;
    opacity: 0.3;
    width: 50%;
}
#SearchContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#SearchContainer > button {
    background: #fff;
    border: 0;
    color: #00b5ad;
    font-weight: bolder;
    cursor: pointer;
    border-radius: 3%;
    margin: 3%;
    font-size: 220%;
    padding: 1%;
    transition-duration: 120ms;
}

#SearchContainer > button:hover {
    transition-duration: 120ms;
    opacity: 0.95;
    -webkit-transform: scale(0.96);
            transform: scale(0.96);
}

#SearchContainer > button:active {
    transition-duration: 120ms;
    -webkit-transform: scale(0.93);
            transform: scale(0.93);
}
#SearchField {
    width: 80%;
    margin-bottom: 2%;
}
#SearchField > #search-input {
    font-weight: 100;
    color: #333;
    margin: 3% 0;
    font-Size: 150%;
    width: 100%;
}
#ContactInfo {
    display: flex;
    color: #333;
    width: 100%;
    font-size: 115%;
    padding-bottom: 2%;

}

#ContactInfo > div {
    flex: 1 1;
    text-align: left;
}

#ContactInfo > #name {
    flex: 3 1;
    font-weight: normal;
}

#ContactInfo > #number {
    flex: 2 1;
    font-weight: 200;
}

#ContactInfo > #editButton {
    background: #fff;
    border: 0;
    color: #00b5ad;
    font-weight: bolder;
    cursor: pointer;
    border-radius: 3%;
    font-size: 100%;
    font-weight: 200;
}

#ContactInfo > #editButton:hover {
    opacity: 0.95;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

#ContactInfo > #editButton:active {
    -webkit-transform: scale(0.90);
            transform: scale(0.90);
}
#AddContactContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#AddContactContainer > #title {
    color: white;
    font-weight: 200;
    margin: 2% 0;
}

#AddContactContainer > h5 {
    margin: 1% 0;
    font-weight: 300;
    font-size: 120%;
}

#AddContactContainer > input {
    font-weight: 100;
    color: #333;
    margin: 2% 0;
    font-Size: 150%;
    width: 80%;
    padding: 1%;
}

#AddContactContainer > button {
    background: #fff;
    border: 0;
    color: #00b5ad;
    font-weight: bolder;
    cursor: pointer;
    border-radius: 3%;
    margin: 3%;
    font-size: 220%;
    padding: 1%;
}

#AddContactContainer > button:hover {
    transition-duration: 150ms;
    opacity: 0.95;
}

#AddContactContainer > button:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
#EditContactContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#EditContactContainer > #title {
    color: white;
    font-weight: 200;
    margin: 2% 0;
}

#EditContactContainer > h5 {
    margin: 1% 0;
    font-weight: 300;
    font-size: 120%;
}

#EditContactContainer > input {
    font-weight: 100;
    color: #333;
    margin: 2% 0;
    font-Size: 150%;
    width: 80%;
    padding: 1%;
}

#EditContactContainer > button {
    background: #fff;
    border: 0;
    color: #00b5ad;
    font-weight: bolder;
    cursor: pointer;
    border-radius: 3%;
    margin: 3%;
    font-size: 220%;
    padding: 1%;
}

#EditContactContainer > button:hover {
    transition-duration: 150ms;
    opacity: 0.95;
}

#EditContactContainer> button:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
